import React, {useEffect,useState} from 'react';
import axios from 'axios';
import "./Listtask";



function GetTodos(){
    
    const [state,setCount]= useState("");
  
    
function myfn() {
    
    console.log("Hello");
    alert("Listed in console");
axios({
       method: "get",
       url:"https://5ff9d9b217386d0017b5226b.mockapi.io/task"
        
  })
        .then((response)=>{setCount(response)
                           console.log(response.data);
                           
                           
                          })
    .catch(err => console.error(err));
    
    }
    

 
    


 
    return(
        <div className="container">
        <h2>List Tasks</h2>
        
        <button onClick={myfn}>List tasks in console</button>
        
       <br/><br/><br/><br/><br/><br/><br/><br/><br/>
        
        
        </div>
        
    )
    
}

export default GetTodos;